import axios from 'axios';
import React, { useEffect, useState } from 'react'

import {Paper, Typography} from '@material-ui/core';
export default function Test() {

 const [data,setData] = useState([]);

    useEffect(()=>{

        let config={
            method:'get',
            url:'http://localhost:5000/api/getcatergory',

        };

        async function MYFetch(x){

            try {
                
                const result=await axios(x);

                console.log(result.data);

                setData(result.data);


            } catch (error) {
                
                console.log(error);

            }
        }

        MYFetch(config);



    },[])
    return (
     <>
     <Paper>
<Typography>
    welcome to test page with Typography
</Typography>

{
    data.map((item)=>{
        return(
            <div key={item._id}>

                <Typography>
                    {
                        item.name
                    }
                </Typography>
<img src={item.image} alt={item.name} style={{width:'200px'}}>
</img>
            </div>
        )
    })
}
     </Paper>
     </>
    )
}
